<template>
  <div class="variate">
    <el-tabs type="border-card" v-model="varType">
      <el-tab-pane name="univariate">
        <span class="tabs-title" slot="label">
          <el-popover placement="right" width="500" trigger="hover">
            <div>
              <p></p>
              <p>
                <b style="color:#409eff"></b>201711
              </p>
              <p>
                <b style="color:#409eff"></b>
              </p>
              <p>
                <b style="color:#409eff"></b>
              </p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </span>
        <univariate :record-id.sync="uniRecordId"></univariate>
      </el-tab-pane>
      <el-tab-pane name="multivariate">
        <span class="tabs-title" slot="label">
          <el-popover placement="right" width="500" trigger="hover">
            <div>
              <p></p>
              <p>
                <b style="color:#409eff"></b>201711
              </p>
              <p>
                <b style="color:#409eff"></b>
              </p>
              <p>
                <b style="color:#409eff"></b>
              </p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </span>
        <multivariate :record-id.sync="multiRecordId"></multivariate>
      </el-tab-pane>
      <el-tab-pane name="ruleanalysis">
        <span class="tabs-title" slot="label">
          <el-popover placement="right" width="500" trigger="hover">
            <div>
              <p></p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </span>
        <ruleanalysis :record-id.sync="ruleRecordId"></ruleanalysis>
      </el-tab-pane>
    </el-tabs>
    <router-link to="/record-list" class="el-button el-button--text btn-link"></router-link>
  </div>
</template>

<script>
import univariate from '@/components/univariate.vue';
import multivariate from '@/components/multivariate.vue';
import ruleanalysis from '@/components/ruleanalysis.vue';

export default {
  name: 'variate',
  components: {
    univariate,
    multivariate,
    ruleanalysis
  },
  data() {
    return {
      uniRecordId: '',
      multiRecordId: '',
      ruleRecordId: ''
    };
  },
  computed: {
    varType: {
      get: function() {
        return this.$store.state.varType;
      },
      set: function(newV) {
        if (newV === this.varType) {
          return;
        }
        this.$store.commit('UPDATE_VAR_TYPE', newV);
      }
    }
  },
  created() {
    const recordId = this.$route.query.recordid;
    if (this.varType === 'univariate') {
      this.uniRecordId = recordId;
    } else if (this.varType === 'multivariate') {
      this.multiRecordId = recordId;
    } else if (this.varType === 'ruleanalysis') {
      this.ruleRecordId = recordId;
    }
  },
  methods: {}
};
</script>

<style scoped>
.variate {
  position: relative;
}
.el-tabs {
  margin: 20px 15px 0;
}
.btn-link {
  position: absolute;
  right: 30px;
  top: 5px;
}
.tabs-title {
  font-size: 16px;
}
</style>
