.btn-comm-del {
  display: inline-block;
  vertical-align: middle;
  margin: 0;
}
.mini-input {
  width: 90px;
}
.mini-input + .mini-input {
  margin-left: 20px;
}
.var-label {
  display: inline-block;
  width: 60px;
}
.box-sample-chart {
  position: absolute;
  top: -20px;
  left: 460px;
  right: 0;
  z-index: 3;
  height: 290px;
  background-color: #ffffff;
}
.box-var-chart {
  position: absolute;
  top: -10px;
  left: 460px;
  right: 0;
  z-index: 3;
  height: 220px;
  background-color: #ffffff;
}
</style>
